import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector, useDispatch } from "react-redux";
import {
  Box,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  IconButton,
  Button,
  Backdrop,
  Typography,
  tableCellClasses,
} from "@mui/material";
import { Close } from "@mui/icons-material";
import {
  onError,
  permissionsLoading,
  permissionsReceived,
  surveyAttributeChanged,
} from "~/state/edit/editState";
import { UserDelete } from "~/components/users/UserDelete";
import { SURVEY, SURVEY_STATUS } from "~/constants/survey";
import { isSurveyAdmin } from "~/constants/roles";
import { AddPermission } from "../AddPermission";
import styles from "./SurveyUsers.module.css";
import { RHFSwitch } from "~/components/hook-form";
import LoadingDots from "~/components/common/LoadingDots";
import { useService } from "~/hooks/use-service";

function SurveyUsers() {
  const surveyService = useService("survey");

  const dispatch = useDispatch();
  const { t } = useTranslation("manage");
  const survey = useSelector((state) => state.editState.survey);
  const fetching = useSelector((state) => state.editState.permissionsLoading);
  const permissionAll = useSelector((state) => state.editState.permissions);
  const isDisabled =
    fetching || !isSurveyAdmin() || survey.status == SURVEY_STATUS.CLOSED;

  const onChangeCheckbox = (e) => {
    dispatch(
      surveyAttributeChanged({
        key: e.target.name,
        value: e.target.checked,
      })
    );
  };

  const loadPermissions = (surveyId) => {
    surveyService
      .getSurveyPermissionAll(surveyId)
      .then((data) => {
        if (data) {
          dispatch(permissionsReceived(data));
        }
        dispatch(permissionsLoading(false));
      })
      .catch((err) => {
        processApirror(err);
      });
  };

  const [userToDelete, setUserToDelete] = useState(null);
  const onCloseModal = () => {
    setUserToDelete(null);
  };

  const [addPermission, setAddPermission] = useState(false);
  const onCloseModalAddPermission = () => {
    setAddPermission(false);
  };

  const processApirror = (e) => {
    dispatch(permissionsLoading(false));
  };

  const deleteUser = () => {
    dispatch(permissionsLoading(true));
    onCloseModal();
    surveyService.removePermission(survey.id, userToDelete.id)
      .then(() => {
        loadPermissions(survey.id);
      })
      .catch((e) => {
        processApirror(e);
      });
  };

  const postPermission = (userId) => {
    onCloseModalAddPermission();
    dispatch(permissionsLoading(true));
    surveyService.addPermission(survey.id, userId)
      .then(() => {
        loadPermissions(survey.id);
      })
      .catch((e) => {
        processApirror(e);
      });
  };

  return (
    <Box className={styles.mainContainer}>
      <Box className={styles.boxContainer}>
        <Typography color="#1a2052" fontWeight="600">
          {t("label.public_within_org")}
        </Typography>
        <RHFSwitch
          disabled={isDisabled}
          name={SURVEY.PUBLIC_WITHIN_ORG}
          checked={survey.publicWithinOrg}
          onChange={onChangeCheckbox}
        />
      </Box>

      <TableContainer
        sx={{
          overflow: "unset",
          [`& .${tableCellClasses.head}`]: {
            color: "text.primary",
          },
          [`& .${tableCellClasses.root}`]: {
            bgcolor: "background.default",
            borderBottomColor: (theme) => theme.palette.divider,
          },
        }}
      >
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell className={styles.tableHeaderCell} align="left">
                {t("users_manage.name")}
              </TableCell>
              <TableCell className={styles.tableHeaderCell} align="left">
                {t("users_manage.email")}
              </TableCell>
              <TableCell className={styles.tableHeaderCell} align="left">
                {t("users_manage.roles")}
              </TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {permissionAll?.map(({ user }) => {
              return (
                <TableRow key={user.id}>
                  <TableCell
                    sx={{
                      color: "#1a2052",
                      fontWeight: "500",
                    }}
                    align="left"
                  >
                    {user.firstName} {user.lastName}
                  </TableCell>
                  <TableCell className={styles.tableBodyCell} align="left">
                    {user.email}
                  </TableCell>
                  <TableCell className={styles.tableBodyCell} align="left">
                    {user.roles.map((role) => t(`label.${role}`)).join(", ")}
                  </TableCell>
                  <TableCell
                    className={styles.tableBodyCell}
                    component="th"
                    scope="row"
                  >
                    <IconButton
                      title={t("users_manage.title_delete")}
                      disabled={isDisabled}
                      onClick={() => {
                        setUserToDelete(user);
                      }}
                    >
                      <Close sx={{ color: "#b9bdcc" }} />
                    </IconButton>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>

      <Button
        className={styles.addUserButton}
        disabled={isDisabled}
        color="primary"
        size="large"
        type="submit"
        variant="contained"
        onClick={() => {
          setAddPermission(true);
        }}
      >
        {t("action_btn.add_user")}
      </Button>
      {Boolean(userToDelete) && (
        <UserDelete
          deleteUser={deleteUser}
          name={userToDelete.firstName + " " + userToDelete.lastName}
          open={Boolean(userToDelete)}
          onClose={onCloseModal}
        />
      )}
      {addPermission && (
        <AddPermission
          currentUserIds={permissionAll.map((permission) => permission.user.id)}
          addPermission={postPermission}
          open={addPermission}
          onClose={onCloseModalAddPermission}
        />
      )}
      <Backdrop open={fetching} sx={{ position: "absolute" }}>
        <LoadingDots />
      </Backdrop>
    </Box>
  );
}

export default React.memo(SurveyUsers);
