import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector, useDispatch } from "react-redux";
import {
  Box,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Stack,
  Button,
  TextField,
  Backdrop,
  tableCellClasses,
} from "@mui/material";
import {
  permissionsLoading,
  permissionsReceived,
  surveyAttributeChanged,
} from "~/state/edit/editState";
import { isSurveyAdmin, isSurveyor } from "~/constants/roles";
import { SURVEY_STATUS } from "~/constants/survey";
import styles from "./SurveyQuota.module.css";
import { RHFSwitch } from "~/components/hook-form";
import LoadingDots from "~/components/common/LoadingDots";
import { useService } from "~/hooks/use-service";

function SurveyQuota() {
  const surveyService = useService("survey");

  const dispatch = useDispatch();
  const { t } = useTranslation("manage");
  const survey = useSelector((state) => state.editState.survey);

  const [checked, setChecked] = useState(survey.quota > 0);
  const [limit, setLimit] = useState(survey.quota >= 0 ? survey.quota : "");

  const changeLimit = (value) => {
    const intValue = parseInt(value);
    if (Number.isInteger(intValue) && intValue >= 0) {
      setLimit(intValue);
      dispatch(
        surveyAttributeChanged({
          key: "quota",
          value: intValue,
        })
      );
    }
  };

  const fetching = useSelector((state) => state.editState.permissionsLoading);
  const permissions = useSelector((state) => state.editState.permissions);
  const permissionAll = permissions?.filter((permission) => {
    return isSurveyor(permission.user);
  });

  const onChangeCheckbox = (e) => {
    if (!e.target.checked) {
      setLimit("");
      dispatch(
        surveyAttributeChanged({
          key: "quota",
          value: -1,
        })
      );
    }
    setChecked(e.target.checked);
  };

  const putPermissionBySurveyor = (userId, quota) => {
    const data = {
      quota,
    };
    dispatch(permissionsLoading(true));
    surveyService
      .editPermission(survey.id, userId, data)
      .then(() => {
        loadPermissions(survey.id);
      })
      .catch((err) => {
        dispatch(permissionsLoading(false));
      });
  };

  const loadPermissions = (surveyId) => {
    surveyService
      .getSurveyPermissionAll(surveyId)
      .then((data) => {
        if (data) {
          dispatch(permissionsReceived(data));
        }
        dispatch(permissionsLoading(false));
      })
      .catch((err) => {
        dispatch(permissionsLoading(false));
      });
  };

  const isDisabled =
    fetching || !isSurveyAdmin() || survey.status == SURVEY_STATUS.CLOSED;

  return (
    <Box className={styles.mainContainer}>
      <>
        <Box className={styles.boxContainer}>
          <Box className={styles.flexContainer}>
            <Typography color="#1a2052" fontWeight="600">
              {t("label.apply_quota")}
            </Typography>
            <TextField
              variant="standard"
              type="number"
              sx={{ width: 200 }}
              disabled={!checked || isDisabled}
              label={t("label.total_responses_limit")}
              value={limit}
              onChange={(e) => {
                changeLimit(e.target.value);
              }}
            />
          </Box>
          <RHFSwitch
            disabled={isDisabled}
            checked={checked}
            onChange={onChangeCheckbox}
          />
        </Box>
        <Box>
          <Typography color="#1a2052" mb="10px" fontWeight="600" variant="h6">
            {t("edit_survey.limit_responses")}
          </Typography>
          <TableContainer sx={{ boxShadow: "none" }} component={Paper}>
            <Table
              sx={{
                overflow: "unset",
                [`& .${tableCellClasses.head}`]: {
                  color: "text.primary",
                },
                [`& .${tableCellClasses.root}`]: {
                  bgcolor: "background.default",
                  borderBottomColor: (theme) => theme.palette.divider,
                },
              }}
              aria-label="simple table"
            >
              <TableHead>
                <TableRow>
                  <TableCell className={styles.tableHeaderCell} align="left">
                    {t("edit_survey.surveyor")}
                  </TableCell>
                  <TableCell className={styles.tableHeaderCell} align="left">
                    {t("users_manage.roles")}
                  </TableCell>
                  <TableCell className={styles.tableHeaderCell} align="left">
                    {t("edit_survey.count")}
                  </TableCell>
                  <TableCell className={styles.tableHeaderCell} align="left">
                    {t("edit_survey.limit")}
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {permissionAll?.map((permission) => {
                  return (
                    <TableRow key={permission.user.id}>
                      <TableCell align="left" className={styles.tableBodyCell}>
                        {permission.user.firstName} {permission.user.lastName}
                      </TableCell>
                      <TableCell className={styles.tableBodyCell} align="left">
                        {permission.user.roles
                          .map((role) => t(`label.${role}`))
                          .join(", ")}
                      </TableCell>
                      <TableCell className={styles.tableBodyCell} align="left">
                        {permission.count}
                      </TableCell>
                      <TableCell className={styles.tableBodyCell} align="left">
                        <UserQuota
                          t={t}
                          quota={permission.quota}
                          isDisabled={isDisabled}
                          onSubmit={(quota) => {
                            putPermissionBySurveyor(permission.user.id, quota);
                          }}
                        />
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
      </>

      <Backdrop open={fetching} sx={{ position: "absolute" }}>
        <LoadingDots />
      </Backdrop>
    </Box>
  );
}

function UserQuota({ t, isDisabled, quota, onSubmit }) {
  const [checked, setChecked] = useState(quota > 0);
  const [limit, setLimit] = useState(quota >= 0 ? quota : "");
  const onChangeCheckbox = (e) => {
    if (!e.target.checked) {
      setLimit("");
    }
    setChecked(e.target.checked);
  };

  const changeLimit = (value) => {
    const intValue = parseInt(value);
    if (Number.isInteger(intValue) && intValue >= 0) {
      setLimit(intValue);
    }
  };
  return (
    <>
      <Box
        className={styles.boxContainer}
        sx={{
          marginBottom: ".5rem",
        }}
      >
        <Typography color="#1a2052" fontWeight="600">
          {t("label.apply_quota")}
        </Typography>

        <RHFSwitch
          disabled={isDisabled}
          checked={checked}
          onChange={onChangeCheckbox}
        />
      </Box>

      <Stack flexDirection="row" alignItems="center">
        <TextField
          type="number"
          variant="standard"
          label={t("label.responses_limit")}
          disabled={!checked || isDisabled}
          value={limit}
          onChange={(e) => {
            changeLimit(e.target.value);
          }}
        />
        <Button
          color="primary"
          size="small"
          type="submit"
          variant="contained"
          onClick={() => onSubmit(limit == "" ? -1 : limit)}
          disabled={(limit == "" && quota < 0) || limit == quota}
        >
          {t("action_btn.save")}
        </Button>
      </Stack>
    </>
  );
}

export default React.memo(SurveyQuota);
