import { Box, Modal, Button } from "@mui/material";
import { useState, useEffect, useMemo } from "react";
import { useTranslation } from "react-i18next";
import { useService } from "~/hooks/use-service";
import styles from "./AddPermission.module.css";

export const AddPermission = ({
  open,
  onClose,
  addPermission,
  currentUserIds,
}) => {
  const userService = useService("user");
  const { t } = useTranslation("manage");
  const [allUsers, setAllUsers] = useState([]);

  const getAllUsers = () => {
    userService.getAllUsers().then((data) => {
      if (data) {
        setAllUsers(data);
      }
    });
  };

  useEffect(() => {
    getAllUsers();
  }, []);

  return (
    <Modal
      sx={{
        ".MuiBackdrop-root": {
          backgroundColor: "rgba(0, 0, 0, 0.3)",
        },
      }}
      open={open}
      onClose={onClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box className={styles.wrapper}>
        {allUsers
          .filter((user) => {
            return currentUserIds.indexOf(user.id) == -1;
          })
          .map((user) => {
            return (
              <Box key={user.id} className={styles.userBlock}>
                <Box>
                  {user.firstName} {user.lastName}
                </Box>
                <Button
                  color="primary"
                  size="small"
                  type="submit"
                  variant="contained"
                  onClick={() => addPermission(user.id)}
                >
                  {t("action_btn.add")}
                </Button>
              </Box>
            );
          })}
        <Box className={styles.action}>
          <Button onClick={onClose}>{t("action_btn.cancel")}</Button>
        </Box>
      </Box>
    </Modal>
  );
};
